 /* featured section */
 .cardPrice:hover{
     -webkit-transform: scale(1.1);
             transform: scale(1.1);
    box-shadow: 5px 6px 22px -6px;
    transition: 1s;
 }

.cardPrice{
    position: relative;
    width: 215px;
    height: 335px;
    background: #ffffff;
    overflow: hidden;
}
.cardPrice::before{
    content: '';
    position: absolute;
    top: -50%;
    width: 100%;
    height: 100%;
    background: #ffffff;
    -webkit-transform: skewY(345deg);
            transform: skewY(345deg);
    transition: 0.5s;
}
.cardPrice:hover::before{
    top: -70%;
    -webkit-transform: skewY(390deg);
            transform: skewY(390deg);
}
.cardPrice::after{
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    font-weight: 600;
    font-size: 8rem;
    color: rgba(0, 0, 0, 0.1);
}
.cardPrice .imgbx{
    position: relative;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    padding-top: 20px;
    z-index: 1;
}
.cardPrice .imgbx img {
    max-width: 100%;
    transition: 0.5s;
}
.cardPrice:hover .imgbx img
{
     max-width: 50%;
}
.cardPrice .contentBx{
    position: relative;
    padding: 22px;
    text-align: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    z-index: 1;
}
.cardPrice .contentBx h3 {
    font-size: 18px;
    color: rgb(0, 0, 0);
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 1px;
}
.cardPrice .contentBx .price{
    font-size: 24px;
    color: rgb(0, 0, 0);
    font-weight: 500;
     text-transform: uppercase;
    letter-spacing: 1px;
}
.cardPrice .contentBx .buyNow {
    position: relative;
    top: 200px;
    opacity: 0;
    padding: 10px 30px;
    margin-top: 15px;
    color: rgb(0, 0, 0);
    text-decoration: none;
    background: #2196f3;
    border-radius: 20px;
    text-transform: uppercase;
    letter-spacing: 1px;
}
.cardPrice:hover .contentBx .buyNow{
top: 0;
    opacity: 1;
}




